<template lang="pug">
    .container.pt-5.pb-5.col-12.col-lg-8.offset-lg-4
        form#id
            span(v-for="e in errors") {{e}}
            section(v-if="step==1")
                h3 Step 1
                input(v-model="form.name",type="text",placeholder="What is your name?").form-control.form-control-lg
            section(v-if="step==2")
                h3 Step 2
                input(v-model="form.phone",type="tel",placeholder="What is your phone number?").form-control.form-control-lg.mb-2
                input(v-model="form.email",type="text",placeholder="What is your email address?").form-control.form-control-lg
            section(v-if="step==3")
                h3 Step 3
                textarea(v-model="form.message",placeholder="Type your message").form-control.form-control-lg
            button(v-if="step != 1",@click.prevent="prevStep") Previous Step
            button(v-if="step != totalsteps",@click.prevent="nextStep") Next Step
            button(v-if="step == 3",@click.prevent="sendEnquiry") Send Enquiry
        .subheading.mt-5 CONSUMO DE API
        p.mb-0 desde "https://jsonplaceholder.typicode.com/users"
        .table-responsive
          table.table.table-striped.mt-3
            thead
                tr
                    th #
                    th Name
                    th City
                    th ZipCode
            tr(v-for="user in users.data")
                td {{user.id}}
                td {{user.name}}
                td {{user.address.city}}
                td {{user.address.zipcode}}     
                   
            
</template>
<script>
import axios from "axios";
export default {
  data: function() {
    return {
      step: 1,
      totalsteps: 3,
      errors: [],
      users: [],
      index: 0,
      form: {
        name: null,
        email: null,
        message: null
      }
    };
  },
  methods: {
    /*
    another method is with a simple v-if
    nextStep: function() {
      if (this.step < 3) {
        this.step++;
      } else {
        alert("No more questions");
        this.step = 1;
      }
    }
    */
    nextStep: function() {
      if (this.step == 1) {
        if (!this.form.name) {
          this.errors = "Please fill out your name";
          return false;
        }
      }
      if (this.step == 2) {
        if (!this.form.phone) {
          this.errors = "Please fill out your phone number";
          return false;
        }
        if (!this.form.email) {
          this.errors = "Please fill out your email";
          return false;
        }
      }
      this.step++;
    },
    prevStep: function() {
      this.step--;
    },
    sendEnquiry: function() {
      if (this.step == 3) {
        if (!this.form.message) {
          this.errors = "Please fill out your message";
          return false;
        }
      }
      this.errors = "";
    },
    //OBTENER API
    async getUsers() {
      axios.get("https://jsonplaceholder.typicode.com/users").then(response => {
        return (this.users = response);
      });
    }
  },
  created() {
    this.getUsers();
  }
};
</script>
